// 
import { combineReducers } from 'redux'

// 
function readinglist(state = [], action) {
    switch (action.type) {
        case 'SAVE_READING_LIST': return [action.val,...state];
        default: return state
    }
}
// 
function tracks(state = [], action) {
    switch (action.type) {
        case 'SAVE_TRACKS': {
            // 
            state.forEach((obj,index)=>{
                if(obj.id===action.val.id){
                    state.splice(index,1);
                    return;
                }
            })
            return [action.val, ...state];
        }
        default: return state
    }
}
// 
function loginUser(state = '/', action) {
    switch (action.type) {
        case 'SAVE_LOGIN': return action.val;
        default: return state
    }
}
export default combineReducers({
    readinglist,
    tracks,
    loginUser
})
