import React, { Component } from 'react'
import { SearchBar, NavBar, Icon, Tag, WingBlank } from 'antd-mobile'
import './search.sass'

export default class Search extends Component {
    render() {
        return (
            <div>
                <NavBar
                    mode="dark"
                    icon={<Icon type="left" />}
                    style={{ background: "#67C23A" }}
                    onLeftClick={() => { this.props.history.push('/') }}
                ></NavBar>
                <SearchBar placeholder="" ref={ref => this.autoFocusInst = ref} />
                <WingBlank size="md">
                    <h4></h4>
                    <div className="tagbox">
                        <Tag></Tag>
                        <Tag></Tag>
                        <Tag></Tag>
                        <Tag></Tag>
                        <Tag></Tag>
                        <Tag></Tag>
                    </div>
                </WingBlank>
            </div>
        )
    }
}
