import React, { Component } from 'react'
import { Carousel, Grid, WingBlank,WhiteSpace, ListView } from 'antd-mobile'
import { Link } from 'react-router-dom'
import { getlist } from '../../../api/api'
import { connect } from 'react-redux'
// 
import Mylist from '../../../components/mylist/Mylist'
import './main.sass'
import { BehaviorSubject } from 'rxjs';

// 
const data = [
    { icon: 'icon-school', text: '', to: 'school' },
    { icon: 'icon-teacher', text: '', to: 'teacher' },
    { icon: 'icon-homework', text: '', to: 'homework' },
    { icon: 'icon-source', text: '', to: 'source' },
    { icon: 'icon-score', text: '', to: 'score' },
    { icon: 'icon-today-reading', text: '', to: 'reading' },
    { icon: 'icon-grown', text: '', to: 'grown' },
    { icon: 'icon-fees', text: '', to: 'fees' },
].map((_val, i) => ({
    icon: require(`../../../assets/images/index-grid/${_val.icon}.png`),
    text: _val.text,
    to: _val.to
}));

 class Default extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: ['banner01.png', 'banner02.jpg', 'banner03.png'],
            imgHeight: 176,
            city: '',
            griddata: data,
            isLoading: true,
            listdata: []
        }
    }


    componentDidMount() {
        // this
        let _this = this;
        //
        function showCityInfo() {
            //
            var citysearch = new window.AMap.CitySearch();
            //IP
            citysearch.getLocalCity(function (status, result) {
                if (status === 'complete' && result.info === 'OK') {
                    if (result && result.city && result.bounds) {
                        _this.setState({
                            city: result.city
                        })
                    }
                } else {
                    _this.setState({
                        city: ''
                    })
                }
            });
        }
        showCityInfo();
        // 
        this.setState({
            data: ['banner01.png', 'banner02.jpg', 'banner03.png']
        });
        // ajax
        getlist().then(res => {
            this.setState({
                listdata: res.data
            })
        })
    }
    render() {
        return (
            <div className='indexbox'>
                {/*  */}
                <div className="searchbox">
                    <Link to='/citylist'>{this.state.city}</Link>
                    <div className="search">
                        <Link to='/search'>
                            <img src={require('../../../assets/images/icon-search.png')} alt="" />
                            <span></span>
                        </Link>
                    </div>
                    <Link to='/map'>
                        <img src={require('../../../assets/images/icon-map.png')} alt="" />
                    </Link>
                </div>
                {/*  */}
                <Carousel
                    autoplay={true}
                    infinite
                >
                    {this.state.data.map(val => (
                        <img
                            key={val}
                            src={require('../../../assets/images/' + val)}
                            alt=""
                            style={{ width: '100%', verticalAlign: 'top' }}
                            onLoad={() => {
                                // fire window resize event to change height
                                window.dispatchEvent(new Event('resize'));
                                this.setState({ imgHeight: 'auto' });
                            }}
                        />

                    ))}
                </Carousel>
                {/*  */}
                <ul className='accountbox'>
                    <li>
                        <img src={require('../../../assets/images/avatar.jpg')} alt="" />
                        <div></div>
                    </li>
                    <li>
                        <p>5</p>
                        <p></p>
                    </li>
                    <li>
                        <p>90</p>
                        <p></p>
                    </li>
                    <li>
                        <p>1000</p>
                        <p></p>
                    </li>
                    <li>
                        <p>10</p>
                        <p></p>
                    </li>
                </ul>
                <WhiteSpace size="md" />
                {/*  */}
                <Grid data={this.state.griddata}
                    hasLine={false}
                    columnNum={4}
                    onClick={_el => window.location.href = `#/${_el.to}`}
                    activeStyle={{ backgroundColor: "#eee" }}
                    renderItem={dataItem => (
                        <div style={{ padding: '.4rem' }}>
                            <img src={dataItem.icon} style={{ width: '4rem', height: '4rem' }} alt="" />
                            <div style={{ color: '#888', fontSize: '1rem' }}>
                                <span>{dataItem.text}</span>
                            </div>
                        </div>
                    )}
                />
                {/*  */}
                <WingBlank size='xl'>
                    <div style={{lineHeight:'3.5rem',color:'#999',textAlign:'center',fontSize:"1.3rem"}}></div>
                </WingBlank>
                <Mylist listdata={this.state.listdata}/>
            </div>
        )
    }
}


// 
export default connect()(Default)