import React, { Component } from 'react'
import { NavBar, Icon, WhiteSpace, WingBlank, List, Radio, Button, Result, Card } from 'antd-mobile'
import { getquestion } from '../../api/api'
import './homework.sass'
// 
const RadioItem = Radio.RadioItem;
// 
const myImg = src => <img src={src} className="spe am-icon am-icon-lg" alt="" />;
// 
let rightvalue, currentscore;
export default class Homework extends Component {
  constructor() {
    super();
    this.state = {
      questions: [
        { id: "1", question: "", a: "", b: "", c: "", d: '', score: 1 }
      ],
      index: 1,
      // 
      value: '',
      btnnext: '',
      btnpre: '',
      score:0,
      right:0,
      btnstatus: true,
      valuebox: [
        { num: 1, value: '', rightvalue: '', currentscore: 0 },
        { num: 2, value: '', rightvalue: '', currentscore: 0 },
        { num: 3, value: '', rightvalue: '', currentscore: 0 },
        { num: 4, value: '', rightvalue: '', currentscore: 0 },
        { num: 5, value: '', rightvalue: '', currentscore: 0 }
      ]
    }
  }
  componentDidMount() {

    getquestion().then(res => {
      this.setState({
        questions: res.data
      })

    })
  }
  onChange = (value) => {
    this.setState({
      value,
    });
  };

  // 
  works() {
    let { index, questions, valuebox, score, right } = this.state;
    // index
    if (index <= 5) {
      let obj = questions[index - 1];
      let answers = [
        { value: 'a', label: obj.a },
        { value: 'b', label: obj.b },
        { value: 'c', label: obj.c },
        { value: 'd', label: obj.d }
      ];
      // 
      rightvalue = obj.answer;
      // 
      currentscore = obj.score
      // 
      return (
        <WingBlank size="lg">
          <List renderHeader={() => obj.question}>
            {answers.map(i => (
              <RadioItem key={i.value} checked={this.state.value === i.value} onChange={() => this.onChange(i.value)}>
                {i.label}
              </RadioItem>
            ))}
          </List>
          <WhiteSpace size="lg" />
          <Button onClick={this.precommit.bind(this)} disabled={this.state.btnstatus} style={{ background: "#67C23A", color: "#fff" }}>{this.state.btnpre}</Button>
          <WhiteSpace size="lg" />
          <Button onClick={this.nextcommit.bind(this)} style={{ background: "#67C23A", color: "#fff" }}>{this.state.btnnext}</Button>
        </WingBlank>
      )

    } else {
    
      return (
        <WingBlank size="lg">
          <Result
            img={myImg(require('../../assets/images/icon-homework.png'))}
            title=""
            message={<p style={{ color: 'red', fontSize: '1.5rem' }}>{score}{right}</p>}
          />
          {/*  */}

          <WhiteSpace size="lg" />
          <Card>
            <Card.Header
              title=""
            />
            <Card.Body>
              <ul>
                {
                  this.state.questions.map(obj => (
                    <li>
                      <p>{obj.question}</p>
                      <p style={{ color: 'red' }}>{obj[obj.answer]}</p>
                    </li>
                  ))
                }
              </ul>
            </Card.Body>
          </Card>
          <WhiteSpace size="lg" />
        </WingBlank>
      )
    }

  }
  // 
  nextcommit() {
    let { index, score, right, valuebox, value, btnnext } = this.state;
    // 
    if (index > 0) {
      this.setState({
        btnstatus: false
      })
    }
    // 
    if (index === 4) {
      this.setState({
        btnnext: ''
      })
    }
    // 
    this.setState({
      index: index + 1
    })
    // 
    this.setState({
      // 
      valuebox: valuebox.map(obj => {
        if (obj.num === index) {
          obj.value = value;
          obj.rightvalue = rightvalue;
          obj.currentscore = currentscore;
        }
        return obj;
      })
    })
    if (btnnext !== '') {
      // 
      this.setState({
        value: valuebox[index].value
      })
    }else{
      // 
      for (const obj of valuebox) {
        if (obj.value === obj.rightvalue) {
          score = score + parseInt(obj.currentscore);
          right=right+1;
        }
      }
      this.setState({
        score: score,
        right: right
      })
    }
  }
  // 
  precommit() {
    let { index, valuebox, value } = this.state;
    // 
    if (index === 2) {
      this.setState({
        btnstatus: true
      })
    }
    // 
    if (index === 5) {
      this.setState({
        btnnext: ''
      })
    }

    // 
    let currentvalue = valuebox.filter(obj => obj.num === index - 1)
    this.setState({
      index: index - 1,
      value: currentvalue[0].value,   // value
      // 
      valuebox: valuebox.map(obj => {
        if (obj.num === index) {
          obj.value = value;
          obj.rightvalue = rightvalue;
          obj.currentscore = currentscore;
        }
        return obj;
      })
    })
  }
  render() {
    return (
      <div className='homeworkbox'>
        <NavBar
          mode="dark"
          icon={<Icon type="left" />}
          style={{ background: "#67C23A" }}
          onLeftClick={() => { this.props.history.push('/') }}
          rightContent={`${this.state.index < 5 ? this.state.index : 5}/5`}//5
        ></NavBar>
        {/* */}
        <WhiteSpace size="lg" />
        <WhiteSpace size="lg" />
        <WhiteSpace size="lg" />
        {this.works()}
      </div>
    )
  }
}
