import axios from 'axios'
import qs from 'qs'

// const IP = 'http://localhost:80'
// const IP = 'http://192.168.43.108:80'
const IP ='http://192.168.2.102:80'


// 
export function login(acc,pwd){
    return axios.post(`${IP}/login.php`, qs.stringify({ acc, pwd }))   
}

// 
export function reg(acc, pwd) {
    return axios.post(`${IP}/reg.php`, qs.stringify({ acc, pwd }))
}
// 
export function getlist(acc, pwd) {
    return axios.get(`${IP}/gethouselist.php`)
}
// 
export function getcontent(id){
    let params={id}
    return axios.get(`${IP}/getcontent.php`, { params})
}
// 
export function getquestion() {
    return axios.get(`${IP}/getquestion.php`)
}
