export function saveReadingList(val) {
    return {
        type: 'SAVE_READING_LIST',
        val
    }
}
// 
export function saveTracks(val) {
    return {
        type: 'SAVE_TRACKS',
        val
    }
}
// 
export function saveLogin(val) {
    return {
        type: 'SAVE_LOGIN',
        val
    }
}
// 
export function changeName(val) {
    return {
        type: 'CHANGE_NAME',
        val
    }
}
